body {
  margin: 0;
  box-sizing: border-box;
  font-family: "Oswald", sans-serif;
}

// WALLPAPER
.wallpaper {
  height: 80vh;
  background-image: url("../img/wallpaper.jpg");
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position-x: 0px;
  background-position: center;
  margin-top: 0px;
  border-bottom: 11px solid black;
}

.signup {
  box-sizing: border-box;
  margin: 0;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(248, 248, 248);
  margin: -100px 250px 0px 250px;
  border-radius: 20px;
  box-shadow: -10px 10px 20px -5px rgb(0, 0, 0);

  img {
    width: 290px;
  }

  div {
    margin-top: 30px;
    width: 290px;
    input {
      width: 290px;
      height: 38px;
      border-radius: 5px;
      border: none;
      box-shadow: -5px 10px 20px -5px rgb(0, 0, 0);
    }
  }

  .terms {
    display: flex;
    input {
      margin-top: 25%;
      width: 200px;
      border: none;
      box-shadow: none;
    }
  }
  .terms p {
    text-align: justify;
    font-size: 14px;
  }

  .SignupButtoni button {
    width: 140px;
    height: 30px;
    font-size: 15px;
    background-color: rgb(0, 0, 0);
    color: white;
    border-radius: 5px;
    margin: -20px 0px 30px 0px;
  }
  .SignupButtoni button:hover {
    background-color: rgb(46, 168, 66);
    color: white;
  }
}

// LOG IN

.login {
  display: flex;
  height: auto;
  box-sizing: border-box;
  flex-direction: row;
  justify-content: space-around;
  background-color: antiquewhite;
  background: rgba(78, 72, 72, 0.2);  
  border-radius: 10px;
  margin: 40px 50px 0px 50px;

  .loginMedia {
    display: flex;
    flex-direction: column;
    width: auto;
    .fb {
      background-color: #3b5998;
      color: white;
    }

    .twitter {
      background-color: #55acee;
      color: white;
    }

    .google {
      background-color: #dd4b39;
      color: white;
    }

    input,
    .btn {
      width: 120%;
      padding: 15px;
      border: none;
      border-radius: 4px;
      margin: 5px 0;
      opacity: 0.85;
      font-size: 17px;
      line-height: 20px;
      text-decoration: none;
    }

    input:hover,
    .btn:hover {
      opacity: 1;
    }
  }

  .loginMedia2 {
    display: flex;
    flex-direction: column;
    width: auto;

    input{
        width: 120%;
      padding: 15px;
      border: none;
      border-radius: 4px;
      margin: 5px 0;
      opacity: 0.85;
      font-size: 17px;
      line-height: 20px;
      text-decoration: none;
    }
    

    input[type=submit] {
        background-color: #04AA6D;
        color: white;
        cursor: pointer;
        width: 60%;
      }
      
      input[type=submit]:hover {
        background-color: #000000;
      }

  }
}


